    .vidBg {
        margin-top: 304px;
        height: 362px;
        background: url(main_movie_cover.png) no-repeat;
        background-size: cover;
        .mainVid {
            text-align: center;
            position: relative;
            top: -173px;
        }
    }

    .article2 {
        .row {
            ul {
                display: flex;
                flex-wrap: wrap; // 줄바꿈
                padding: 30px 0;
                li {
                    margin: 0 auto;
                    // width: 15%;
                    padding: 20px;
                    text-align: center;

                    a {
                        color: #fff;
                        background-color: #23cec2;
                        border-radius: 100px;
                        height: 100px;
                        width: 100px;
                        display: flex;
                        &:hover {
                            background: #ed1b23;
                        }
                        div {
                            font-weight: bold;
                            margin: 0 auto;
                            align-self: center;
                            // 부모높이의 중간으로 세로정렬
                        }
                    }
                }
            }
            .lifeStyle {

                h2 {
                    font-size: 20px;                    
                }
                ul {
                    display: flex;
                    li {
                        width: 33.33%;
                        padding: 10px;
                        cursor: pointer;
                        &:hover {
                            opacity: 0.5;
                            transition: all 0.5s;
                        }
                        img {
                            width: 100%;
                            border-radius: 10px;
                        }
                    }
                }
            }
        }
    }
