.headerWrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: #fff;
    transition: all 0.5s;
    z-index: 9;
    .row {
        display: flex;
        height: 80px;
        justify-content: space-between;
        position: relative;
        align-items: center; // 높이 안에서 세로 중간정렬, 자식들 공통으로전체에게 줄땐 부모에게 align-items: center;, 본인에게 줄땐 align-self: center;
        .nav {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            .depth1 {
                display: flex;
                li {
                    position: relative;
                    padding: 20px 0; // hover했을때 간격주기, depth2에 주면 간격때문에 메뉴가 닫혀서 li에 padding으로 줌
                    cursor: pointer;
                    margin: 0 30px;
                }
            }
            .depth2 {
                position: absolute;
                top: 100%;
                left: 50%;
                width: 200px;
                background: #fff;
                transform: translate(-50%);
                padding: 20px 30px;
                color: #000;
                border-radius: 10px;
                display: none;
                &.on {
                    display: block;
                }
                li {
                padding: 10px 0;
                margin: 0;
                }
            }
        }
        .mobileNav {
            position: relative;
            button {
                padding: 10px 20px;
                border-radius: 50px;
                border: none;
                font-size: 20px;
                display: flex;
                cursor: pointer;
                .ham {
                    margin-right: 10px;
                }
                .ham, .person {
                    align-self: center;
                }
            }
            .mobileNavList {
                position: absolute;
                top: 100%;
                right: 0;
                margin-top: 10px;
                background: #fff;
                padding: 10px 0px;
                color: #000;
                width: 150px;
                border-radius: 10px;
                display: none;
                &.opener { // 자기자신(&=mobileNavList)에 .opener가 추가되면 보여지게 
                    display: block;
                }
                ul {
                    li {
                        line-height: 30px;
                        padding: 0 20px;
                    }
                    li:nth-child(3) {
                        border-top: 1px solid #ddd;
                        padding-top: 10px;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}